.content {
  width: 100%;
  height: 100%;
  background: #f6f6f6;
}

.App {
  max-width: 500px;
  min-height: calc(100vh - 48px);
  padding: 0 24px;
  height: 100%;
  background-color: #fff;
  margin: 0 auto;
}

.title {
  width: 100%;
  line-height: 24px;
  font-size: 16px;
}

.header {
  height: 48px;
  background: #fff;
  text-align: center;
  border-bottom: 1px solid #f6f6f6;
  display: flex;
  align-items: center;
  justify-content: center;
}

.headImg {
  height: 36px;
  width: 36px;
  margin-right: 12px;
}

.finishOrLearn {
  text-align: center;
  line-height: 100px;
}

.checkbox {
  width: 100%;
}
.check {
  width: auto;
  height: 24px;
}

.btn {
  padding-top: 24px;
}
